.iconSelectWrapper {
  position: relative;
  background-color: #bad4aa;
  border: solid 1px #bad4aa;
  min-height: 460px;
  height: calc(150vw - 20px);
  max-height: calc(100vh - 20px);
  min-width: 300px;
  width: calc(66vh - 20px);
  max-width: calc(100vw - 20px);
  margin: 0 auto;
  top: 50%;
  transform: translateY(-50%); }

.iconSelect {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-rows: 7.5% 45% 30% 10%;
  row-gap: 2.5%;
  padding: 3.87%; }

.heading {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  color: #000000;
  font-weight: 300;
  font-size: calc(19px + 20 * ((100vw - 320px) / 280)); }

.highlight {
  font-weight: 800; }

.button, .userConfirmButton, .compConfirmButton, .backButton {
  min-height: 40px;
  height: 100%;
  font-size: calc(16px + 10 * ((100vw - 320px) / 280));
  color: #000000;
  border: none;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
  transition: all 300ms ease-in-out;
  cursor: pointer;
  padding: 0;
  font-family: "BioRhyme", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  position: relative; }

.userConfirmButton {
  background-color: #7be0ad;
  width: 100%; }

.compConfirmButton {
  background-color: #7be0ad;
  width: 80%; }

.compConfirmButton:disabled {
  cursor: not-allowed; }

.compConfirmButton:hover,
.userConfirmButton:hover,
.compConfirmButton:focus,
.userConfirmButton:focus {
  background-color: #8ceabb; }

.backButton {
  width: 18.5%;
  color: #fff;
  background-color: #DA7606;
  float: left; }

.backButton:hover, .backButton:focus {
  background-color: #f4b269; }

.arrow {
  height: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

@media (orientation: portrait) and (min-width: 600px) {
  .heading {
    font-size: calc(43px + 10 * ((100vw - 600px) / 624)); }
  .button, .userConfirmButton, .compConfirmButton, .backButton {
    font-size: calc(26px + 10 * ((100vw - 320px) / 624)); } }

@media (orientation: landscape) {
  .iconSelectWrapper {
    min-height: 300px;
    height: calc(66vw - 20px);
    max-height: calc(100vh - 20px);
    min-width: 460px;
    width: calc(150vh - 20px);
    max-width: calc(100vw - 20px); }
  .iconSelect {
    grid-template-rows: 7.5% 70% 14.78%;
    grid-template-columns: 75% 1fr;
    grid-template-areas: "header header" "editor showcase" "button button";
    row-gap: 3.86%;
    column-gap: 2.5%;
    padding: 2.5%; }
  .heading {
    grid-area: header;
    font-size: 25px; }
  .button, .userConfirmButton, .compConfirmButton, .backButton {
    font-size: 18px; }
  .buttonRow {
    grid-area: button;
    display: flex;
    justify-content: space-between; } }

@media (orientation: landscape) and (min-height: 600px) {
  .heading {
    font-size: 40px; }
  .button, .userConfirmButton, .compConfirmButton, .backButton {
    font-size: 30px; } }

@media (orientation: landscape) and (min-height: 768px) and (min-width: 1024px) {
  .heading {
    font-size: 50px; }
  .button, .userConfirmButton, .compConfirmButton, .backButton {
    font-size: 40px; } }
