.iconEditor {
  height: 100%;
  width: 100%;
  margin: 0 auto;
  background-color: #fff;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
}

.canvas {
  float: left;
  height: 100%;
  width: 75%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.canvasInner {
  height: 100%;
  width: 100%;
}

.palette {
  position: relative;
  height: 100%;
  width: 25%;
  float: right;
}

.palette::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: transparent;
  opacity: 0.3;
  pointer-events: none;
}

.palette:hover::after,
.palette:focus-within::after {
  background-color: #fff;
  border: solid 5px #000;
  box-sizing: border-box;
}

.paintIcon {
  position: absolute;
  z-index: 1;
  height: auto;
  width: 75%;
  left: 12.5%;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none;
}

.paintImg {
  height: 100%;
  width: 100%;
  pointer-events: none;
}

input[type="color"] {
  display: block;
  -webkit-appearance: none;
  border: none;
  width: 100%;
  height: 100%;
  cursor: pointer;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
}

input[type="color" i] {
  padding: 0;
}

input[type="color"]::-webkit-color-swatch {
  border: none;
}

input[type="color"]::-webkit-color-swatch-wrapper {
  border: none;
  padding: 0;
}
