.app {
  text-align: center;
  height: 100%; }

.appBody {
  position: relative;
  height: 100%;
  width: 100%; }

.reactCSSTransitionGroup {
  height: 100%;
  width: 100%;
  position: absolute;
  top: -100%;
  left: 0;
  z-index: 1; }

.gameScreenWrapper {
  height: 100%;
  width: 100%;
  padding: 2.083vh; }

.gameScreen {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  height: auto; }

@media (orientation: landscape) {
  .heading {
    font-size: 5vh; }
  .gameScreenWrapper {
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    margin: 0 auto;
    padding: 2.08%;
    min-height: 300px;
    height: 63.47vw;
    max-height: 99vh;
    min-width: 460px;
    width: 146.47vh;
    max-width: 94.07vw; }
  .gameScreen {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: repeat(3, 1fr);
    grid-template-areas: "grid grid preview" "grid grid controls" "grid grid controls";
    grid-row-gap: 3.33%;
    row-gap: 3.33%;
    grid-column-gap: 2.17%;
    -webkit-column-gap: 2.17%;
            column-gap: 2.17%;
    height: 100%; } }

@media (orientation: portrait) and (max-aspect-ratio: 500 / 1000) {
  .heading {
    font-size: 4vh; } }

@media (orientation: portrait) and (min-aspect-ratio: 501 / 1000) {
  .heading {
    font-size: 5vh; } }

@media (orientation: landscape) and (max-height: 320px), (orientation: landscape) and (max-width: 480px) {
  .gameScreen {
    grid-template-columns: 145px 145px 1fr;
    grid-template-rows: repeat(3, 93.333px);
    grid-gap: 10px;
    height: 300px; }
  .gameScreenWrapper {
    padding: 10px; } }

@media (orientation: portait) and (max-width: 320px), (orientation: portait) and (max-height: 480px) {
  .gameScreenWrapper {
    padding: 10px; } }
