* {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: "BioRhyme", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ebf5df;
  height: 100%; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

h2 {
  font-size: 1.5rem;
  margin: 1.245rem; }

#root {
  min-height: 480px;
  height: 100%;
  min-width: 320px;
  overflow: hidden; }

.slide-enter {
  transform: translateX(-100%); }

.slide-enter-active {
  transform: translateX(0);
  transition: transform 300ms linear; }

.slide-leave {
  transform: translateX(0);
  transition: transform 300ms linear; }

.slide-leave-active {
  transform: translateX(-100%); }

.fade-enter {
  opacity: 0.01; }

.fade-enter-active {
  opacity: 1;
  transition: opacity 500ms linear; }

.fade-leave {
  opacity: 1; }

.fade-leave-active {
  opacity: 0.01;
  transition: opacity 200ms linear; }

@media (orientation: landscape) {
  #root {
    min-width: 480px;
    min-height: 320px; } }
